export interface CopyButtonProps {
  /**
   * Text to copy to clipboard
   */
  text: string;
  
  /**
   * Button variant
   */
  variant?: 'primary' | 'secondary' | 'ghost';
  
  /**
   * Button size
   */
  size?: 'sm' | 'md' | 'lg';
  
  /**
   * Additional CSS classes
   */
  className?: string;
  
  /**
   * Show only icon (no text)
   */
  iconOnly?: boolean;
}
